@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap');

.carousel {
  display: inline-block;
  width: 60%;
  height: 500px;
  margin-bottom: 25px;
  margin-right: 15px;
}

.textContainer {
  text-align: center;
}
.text {
  font-size: 200%;
  font-family: 'Comfortaa', cursive;
  color: #2bffc6;
  margin-bottom: 5px;
}

.carouselInner {
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
}

.carouselInner .left {
  flex: 5%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.6);
  display: grid;
  place-items: center;
  color: white;
  cursor: pointer;
}

.carouselInner .center {
  flex: 80%;
  height: 100%;
  display: grid;
  place-items: center;
  font-family: Arial, Helvetica, sans-serif;
  text-align: justify;
  text-align-last: center;
}

.carouselInner .right {
  flex: 5%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.6);
  display: grid;
  place-items: center;
  color: white;
  cursor: pointer;
}
