@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cuprum:ital@1&display=swap');

body {
  background: linear-gradient(rgba(15, 15, 15, 0.7), #0f0f0f 100%),
    url('https://cdn.discordapp.com/attachments/870313330319949895/870363741823324160/2021-07-30_02.52.29.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: top;
}

a {
  text-decoration: none;
}
.container {
  padding: 30px;
  text-align: center;
}

.serverName {
  display: inline-block;
  padding: 50px;
}

.name {
  font-size: 500%;
  font-family: 'Comfortaa', cursive;
  color: #2bffc6;
}

.tagline {
  font-size: 200%;
  font-family: 'Cuprum', sans-serif;
  color: #f9f9f9;
}

#ipButton {
  cursor: pointer;
}

.button {
  border-radius: 25px;
  text-align: center;
  font-family: 'Comfortaa', cursive;
  vertical-align: middle;
  line-height: 40px;
  font-size: 150%;
  text-decoration: none;
  color: rgb(0, 0, 0);
  background-color: #2bffc6;
  height: 50px auto;
  position: relative;
  margin-bottom: 5px;
  transition: 0.2s linear;
}

.button:hover {
  background-color: #22cc9e;
}

.navigationContainer {
  padding: 30px;
  text-align: center;
}
.navigation {
  display: inline-block;
  margin: 30px;
  width: 50%;
}

#side-checkbox {
  display: none;
}
.side-panel {
  position: fixed;
  z-index: 999999;
  top: 0;
  left: -360px;
  background: #0f0f0f;
  transition: all 0.5s;
  width: 320px;
  height: 100vh;
  box-shadow: 10px 0 20px rgba(0, 0, 0, 0.4);
  color: #fff;
  padding: 40px 20px;
}
.side-title {
  font-size: 20px;
  padding-bottom: 10px;
  margin-bottom: 20px;
  border-bottom: 2px solid #bfe2ff;
}
.header {
  display: flex;
  justify-content: space-between;
}

.side-button-1 {
  display: inline-block;
}

.side-button-1 .side-b {
  position: relative;
  margin: 22px 30px;
  cursor: pointer;
}

.side-button-1 .side-b:hover,
.side-button-1 .side-b:active,
.side-button-1 .side-b:focus {
  color: #fff;
}

.side-button-1 .side-b:after,
.side-button-1 .side-b:before {
  position: absolute;
  height: 4px;
  left: 50%;
  bottom: -6px;
  content: '';
  transition: all 280ms ease-in-out;
  width: 0;
}

.side-button-1 .side-b:before {
  top: -6px;
}

.side-button-1 .side-b:hover:after,
.side-button-1 .side-b:hover:before {
  width: 100%;
  left: 0;
}

/* Переключатели кнопки 1 */
.side-button-1 .side-close {
  display: none;
}

#side-checkbox:checked
  + .side-panel
  + .side-button-1-wr
  .side-button-1
  .side-open {
  display: none;
}

#side-checkbox:checked
  + .side-panel
  + .side-button-1-wr
  .side-button-1
  .side-close {
  display: block;
}

#side-checkbox:checked + .side-panel {
  left: 0;
}

/* Оформление кнопки на панеле */
.side-button-2 {
  font-size: 30px;
  border-radius: 20px;
  position: absolute;
  z-index: 1;
  top: 8px;
  right: 8px;
  cursor: pointer;
  transform: rotate(45deg);
  color: #bfe2ff;
  transition: all 280ms ease-in-out;
}

.side-button-2:hover {
  transform: rotate(45deg) scale(1.1);
  color: #fff;
}

#menuIcon {
  width: 50px;
  height: 50px;
}

body::-webkit-scrollbar {
  width: 12px; /* ширина scrollbar */
}
body::-webkit-scrollbar-track {
  background: #0f0f0f; /* цвет дорожки */
}
body::-webkit-scrollbar-thumb {
  background-color: #2bffc6; /* цвет плашки */
  border-radius: 20px; /* закругления плашки */
  border: 0px; /* padding вокруг плашки */
}
